import { useState } from "react";

function Login({SendPassword}) {
    const [password, setPassword] = useState("");

    return (
        <div className="card">
            <input
                value={password}
                onChange={({ target }) => {
                    setPassword(target.value);
                }}
                name="password"
                type="text"
            />
            
            <button 
                className="btn submit-btn" 
                onClick={() => SendPassword(password) }
            >
                Go
            </button>
        </div>
    );
}

export default Login;