import CryptoJS from "crypto-js";


function toString(words){
    return CryptoJS.enc.Utf8.stringify(words);
}

function toBase64String(words){
    return CryptoJS.enc.Base64.stringify(words);
}

export function encryptData(input, key){
    var secret_key = CryptoJS.SHA256(key);
    var iv = CryptoJS.lib.WordArray.random(16);
    var options = {
        iv: iv,
        mode: CryptoJS.mode.CBC
    }
    var body = CryptoJS.AES.encrypt(input, secret_key, options);
    iv.concat(body.ciphertext);
    return toBase64String(iv);
}

export function decryptData(input, key){
    var packet = CryptoJS.enc.Base64.parse(input);

    var secret_key = CryptoJS.SHA256(key);
    var iv = CryptoJS.lib.WordArray.random(16);

    var start = iv.words.length;
    var end = packet.words.length;
    
    var ciphertext = CryptoJS.lib.WordArray.create(packet.words.slice(start, end));
    var parsed_iv = CryptoJS.lib.WordArray.create(packet.words.slice(0, iv.words.length+1));
    ciphertext = toBase64String(ciphertext);
    var options = {
        iv: parsed_iv,
        mode: CryptoJS.mode.CBC
    }
    var decrypted = CryptoJS.AES.decrypt(ciphertext, secret_key, options);

    return toString(decrypted);
}